.patchnotes{display:flex;flex-direction:column;align-items:center}.patchnotes .banner{height:370px}.patchnotes_container{margin:220px 0 0;background-color:#17191b;padding:40px;width:100%;max-width:1100px;gap:30px}.patchnotes_container_header{font-size:1.3em;text-align:center;margin-bottom:50px}.patchnotes_container .context{font-size:.8em;text-align:justify}.patchnotes_container .context::before{content:"„";font-size:1.2em;color:gray}.patchnotes_container .context::after{content:"”";font-size:1.2em;color:gray}.patchnotes_container div.context-designers{margin-left:40px;color:#dcdcdc;font-size:.7em}.patchnotes_container div.context-designers span{display:flex;align-items:center;gap:10px}.patchnotes_container div.context-designers span img{width:30px;border-radius:50%}.patchnotes_container hr,.patchnotes_container div.content-border{border:1px solid #242b30}.patchnotes_container div.content-border{display:flex;padding:20px 30px}.patchnotes_container div.content-border>div>div>span.content-border{display:flex;width:100%;justify-content:center}.patchnotes_container h4{margin-bottom:0;margin-left:40px;font-weight:800}.patchnotes_container li{font-size:.8em;margin-bottom:4px;margin-left:20px}.patchnotes_container div.gs-container{display:flex;margin-bottom:10px;justify-content:center;gap:20px}.patchnotes_container div.gs-container img{width:450px}.patchnotes_container header{display:flex;justify-content:center}.patchnotes_container header h2{font-size:2em;font-weight:bold}.patchnotes_container img{max-width:100%}@media screen and (max-width: 700px){.patchnotes_container{padding:20px}.patchnotes_container h1{font-size:1.4em !important}.patchnotes_container h2{font-size:1.4em !important}.patchnotes_container div.gs-container img{width:100%}.patchnotes_container span.content-border{padding:0 !important}.patchnotes_container span.content-border img{width:100% !important;height:auto !important}.patchnotes_container blockquote,.patchnotes_container h4{margin:15px 0 !important}.patchnotes_container div.context-designers{margin:0 !important}.patchnotes_container ul{padding:0}.patchnotes_container .content-border{padding:15px !important}}